* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
  overflow: hidden;
}

#canvas {
  box-shadow: 1px 1px 3px #fffcf5;
  border-radius: 2px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -2;
}
.button {
  box-shadow: 1px 1px 2px #f55b98;
  background-color: #e61266;
  border: none;
  color: white;
  padding: 0.3vw 0.6vw;
  text-align: center;
  text-decoration: none;
  font-size: 12px;
  border-radius: 15px;
  width: 60px;
  height: 40px;
  margin: 2px;
}
.button:hover {
  transform: scale(1.1);
  transition: 0.3s;
  transition-timing-function: ease-in-out;
}

#buttons-div {
  height: 100px;
}

#history-div button {
  background-color: #f2a7cc;
}
#history-div {
  display: flex;
  align-items: flex-end;
  position: absolute;
  bottom: 0;
  right: 0;
}
